@import "../colors";
@import "../typography";
@import "../mixins";
@import "../icons/googleIcons";

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	list-style: none;
	text-decoration: none;
	outline: none;
}

html {
	font-size: 62.5%;
	scroll-behavior: smooth;
}

body {
	background-color: $light-cultured;
	color: $onyx;

	@include font-small;
}

figure {
	display: flex;
}

section[id] {
	scroll-margin-block-start: 7rem;
}

img {
	width: 100%;
	height: 100%;
}

a {
	color: $onyx;
}

